import React from "react";
import { useContext } from "react";
import { Chessboard } from "react-chessboard";
import { PuzzleContext } from "./Root";
import {
  getCustomSquareStyles,
  isLegalMove,
  requiresPromotion,
} from "../utils/utils";
import { Move, Square } from "chess.js";
import { PromotionPieceOption } from "react-chessboard/dist/chessboard/types";

export interface BoardProps extends React.ComponentProps<typeof Chessboard> {}
export const Board: React.FC<BoardProps> = ({ ...rest }) => {
  const puzzleContext = useContext(PuzzleContext);
  if (!puzzleContext) {
    throw new Error("PuzzleContext not found");
  }

  const [activeSquare, setActiveSquare] = React.useState<Square | null>(null);
  const [promotionMove, setPromotionMove] =
    React.useState<Partial<Move> | null>(null);

  const onSquareClick = (square: Square) => {
    if (["solved", "failed"].includes(status)) {
      return;
    }

    if (activeSquare === null) {
      return setActiveSquare(game.moves({ square }).length ? square : null);
    }

    if (!isLegalMove(game, `${activeSquare}${square}`)) {
      return setActiveSquare(game.moves({ square }).length ? square : null);
    }

    if (requiresPromotion(game, `${activeSquare}${square}`)) {
      setPromotionMove({
        from: activeSquare,
        to: square,
      });
      return console.log("requires promotion dialog");
    }
    handlePieceDrop(activeSquare, square);
    setActiveSquare(null);
  };

  const onPromotionPieceSelect = (piece?: PromotionPieceOption): boolean => {
    if (promotionMove?.from && promotionMove?.to && piece) {
      setPromotionMove(null);
      return handlePieceDrop(promotionMove?.from, promotionMove?.to, piece);
    }
    return false;
  };

  const {
    game,
    status,
    lastMove,
    nextMove,
    orientation,
    handlePieceDrop,
    hint,
    isPlayerTurn,
  } = puzzleContext;

  return (
    <Chessboard
      customSquareStyles={getCustomSquareStyles(
        status,
        hint,
        isPlayerTurn,
        activeSquare,
        nextMove,
        lastMove
      )}
      boardOrientation={orientation}
      position={game.fen()}
      showPromotionDialog={!!promotionMove}
      onPromotionPieceSelect={
        !!promotionMove ? onPromotionPieceSelect : undefined
      }
      onPieceDrop={(sourceSquare, targetSquare, piece) =>
        handlePieceDrop(sourceSquare, targetSquare, piece)
      }
      onSquareClick={onSquareClick}
      areArrowsAllowed={true}
      animationDuration={status === "not-started" ? 0 : 300}
      {...rest}
    />
  );
};
